#header{
  width: 100vw;
  height: 100px;
  position: fixed;
  padding: 20px 30px 20px 30px;
  display: flex;
  align-items: flex-end;
  background-color: transparent;
  -webkit-transition: all ease 0.7s;
  -moz-transition: all ease 0.7s;
  -o-transition: all ease 0.7s;
  transition: all ease 0.7s;
  z-index: 10;

  .logo{
    width: fit-content;
    height: 40px;
    font-size: 4rem;
    margin-right: 8%;
    
    .logoLink{
      color: #D30155;
      font-weight: bold;
    }
  }

  .nav{
    width: fit-content;
    height: auto;

    .link{
      font-size: 1.7rem;
      margin:0  40px;
      font-weight: 500;
      &:hover{
        color: #D30155;
      }
    }
  }

  .nav2{
    display: none;
    width: 100vw;
    height: 100vh;
    position: absolute; left: 0; top: 0;
    background-color: rgba(255,255,255, 0.5);
    backdrop-filter: blur(5px);
    transition: all ease 0.7s;
    overflow: hidden;

  .center{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);

    .link{
      display: block;
      font-size: 4rem;
      margin:40px  0;
      font-weight: bold;
      text-align: center;
      color: rgba(0,0,0, 0.7);
      letter-spacing: 5px;
      &:hover{
        color: #D30155;
      }
    }


  }
  .close{
    font-size: 3rem;
    position: absolute; top: 5%; right: 5%;
    cursor: pointer;
  }
  }
  

  .icons{
    margin-left: auto;
    .search{
      font-size: 2rem;
      cursor: pointer;
      display: inline-block;

    }
    
    .bars{
      display: none;
      font-size: 2rem;
      cursor: pointer;
      margin-left: 10px;
    }
  }

  .searchBox{
    position: absolute; left: 0; top: -50vh;
    width: 100vw;
    height: 50vh;
    background-color: rgba(255,255,255, 0.6);
    backdrop-filter: blur(5px);
    text-align: center;
    transition: all ease 0.5s;

    .title{
      font-size: 2.5rem;
      padding-top: 10%;
      font-weight: bold;
      margin-bottom: 20px;
    }

    .inputBox{
      position: relative;
      width: 70%;
      height: 10%;
      margin: 0 auto 30px;
      .searchInput{
        width: 100%;
        height: 100%;
        border-radius: 7px;
        border: 1px solid rgba(204,204,204, 0.7);
        padding-left: 10px;
        font-size: 1.6rem;
        
        &:focus{
          outline: none;
        }
      }

      .search{
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 1.5rem;
      }

    }

    .trend{
      width: 70%;
      margin: 0 auto;
      display: flex;
      align-items: center;
      .title2{
        font-size: 2.3rem; 
        height: fit-content;
        padding-right: 20px;
        .span{font-weight: bold;}
      }
      .hashBox{
        display: flex;

        .hash{
          border: 1px solid rgba(204,204,204, 0.7);
          padding: 7px 15px; 
          border-radius: 25px;
          font-size: 1.3rem;
          font-weight: 500;
          margin-right: 20px;
          background-color: #fff;
          cursor: pointer;
        }
      }
    }

    .close{
      position: absolute; right: 5%; top: 10%;
      font-size: 3rem;
      cursor: pointer;
    }
    
  }

}


@media screen and (max-width : 720px) {
  #header .nav{
    display: none;
  }

  #header .icons .bars{
    display: inline-block;
  }
}

@media screen and (max-width : 680px) {
  #header .searchBox .trend{
    flex-direction: column;

    .title2{margin-bottom: 10px;}
  }
}