.footer{
  width: 100vw;
  display: flex;
  padding: 70px 0 50px;
  background: #e1e1e1;
  justify-content: space-around;
  
  .left{
    display: flex;

    .logo{
      font-size: 3rem;
      font-weight: bold;
      color: #D30155;
      padding-right: 20px;
    }

    .information{
      .tr{
        font-size: 1.3rem;
        margin-bottom: 5px;
      }
    }
  }

  .right{
    display: flex;

    .title{
      font-size: 2rem;
      font-weight: bold;
      padding-right: 20px;
    }

    .contact{
      .phone{
        display: flex;
        align-items: center;
        margin-bottom: 5px;

        .icon{
          padding-right: 20px;
          font-size: 1.8rem;
        }

        .text{
          .tr{
            font-size: 1.3rem;
          }
        }
      }
    }
  }
}

@media screen and (max-width : 660px) {
  .footer{
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .left{
      margin-bottom: 40px;
    }
  }
}