.section3{
  padding-top: 100px;
  width: 100%;
  height: auto;
  background: url('https://seungeonn.github.io/data/project1/image/main/section3/bg.jpg') no-repeat 0 0;
  margin-bottom: 100px;

  .text{
    text-align: center;
    margin-bottom: 100px;

    .h2{
      font-weight: bold;
      font-size: 3rem;
      margin-bottom: 50px;
    }

    .p{
      font-size: 1.8rem;
      margin-bottom: 50px;
      font-weight: 400;
    }

    .viewBtn{
      display: inline-block;
      padding: 15px 25px ;
      font-size: 1.8rem;
      font-weight: bold;
      text-align: center;
      background: #272727;
      color: #fff;
      border-radius: 10px;
      border: 1px solid #000;
      cursor: pointer;

      &:hover{
        opacity: 0.7;
      }

      .btn{
        color: #fff;
      }
    }

  }

  .imgBox{
    width: 38%;
    height: 550px;
    margin: auto;
    margin-bottom: 150px;

    .img{
      object-fit: contain;
    }
  }

  .aniText{
    width: 100%;
    height: auto;
    transition: all ease 1s;
    margin-bottom: 150px;
    position: relative; top: -200px;
    opacity: 0;
  }
  
  .product{
    display: flex;
    align-items: center;
    justify-content: space-around;

    .left, .right{
      width: 48%;
      height: 400px;
      overflow: hidden;
      position: relative;

      .deco{
        width: 45px;
        position: absolute; left: 3%; top: 3%;
        font-size: 1.5rem;
        font-weight: 600;
        background: #555555;
        padding: 8px 0px;
        border-radius: 3px;
        color: #fff;
        text-align: center;
      }
      
      .img{
        transition: all ease 0.5s;

        &:hover{
          transform: scale(1.1);
        }
      }

    }

  }
}

@media screen and (max-width : 700px) {
  .section3 .text{
    margin-bottom: 30px;

    .h2{margin-bottom: 10px;}
  }

  .section3 .imgBox{
    margin-bottom: 0px;
    width: 50%;
  }

.section3 .product .left, .section3 .product .right{
  height: 300px;
}

}