#detail2{
  width: 100vw;
  height: auto;

  .section1{
    width: 100%;
    height: auto;
    display: flex;

    .img{
      width: 100%;
      height: 100%;
    }
  
    .left{
      flex: 1;
      height: 100vh;
      position: sticky; top: 0;
      overflow: hidden;
  
      .big-box{
        display: flex;
        position: absolute; left: -0vw; top: 0;
        transition: all ease 1s;
        .box{
          flex: none;
          width: 50vw;
          height: 100vh;

          .img{
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
      }

      .current{
        width: 100%;

        .long{
          width: 90%;
          height: 3px;
          background: rgba(255,255,255, 0.7);
          position: absolute; bottom: 50px;
          left: 50%;
          -webkit-transform: translateX(-50%);
          -moz-transform: translateX(-50%);
          -o-transform: translateX(-50%);
          transform: translateX(-50%);

          .short{
            height: 5px;
            background: #fff;
            position: absolute; top: -1px;
          }
        }


      }
    }
  
  
    .right{
      background-color: #fff;
      flex: 1;
      width: 50%;
      height: auto;
      padding-top: 200px;
      padding-left: 2%;
      padding-right: 2%;
      background: url('https://seungeonn.github.io/data/project1/image/detail2/bg.png') no-repeat right top;
  
      .text{
        margin-bottom: 150px;
        .title{
          color: #171c8f;
          font-size: 5rem;
          font-weight: bold;
          font-family: 'Rowdies', cursive;
          letter-spacing: -3px;
          margin-bottom: 10px;
        }

        .subTitle{
          display: inline-block;
          font-size: 2rem;
          font-weight: bold;
          margin-right: 20px;
        }

        .hash{
          display: inline-block;
          font-size: 1.3em;
          padding: 6px 10px;
          background-color: rgba(255,255,255, 0.7);
          border: 1px solid #ccc;
          border-radius: 20px;
          margin-right: 10px;
        }
      }
      

      .product{
        width: 100%;

        .title{
          font-size: 3rem;
          font-weight: bold;
          margin-bottom: 50px;
          font-family: 'Rowdies', cursive;
        }

        .box{
          display: flex;
          margin-bottom: 30px;
          .leftBox{
            flex: 5;
            padding: 15% 2% 0;
            background-color: #fafafa;
            border-radius: 7px;
            .name{
              font-size: 1.8rem;
              font-weight: bold;
              margin-bottom: 50px;
            }
            .price{font-size: 1.3rem;}
          }

          .rightBox{
            flex: 4.5;
            overflow: hidden;
            border-radius: 7px;

            .img{
              border-radius: 7px;
              transition: all ease 0.3s;
              cursor: pointer;

              &:hover{
                transform: scale(1.1);
              }
            }
          }
        }
      }
    }
  }

}

@media screen and (max-width : 720px) {
  #detail2{
    .section1{
      flex-direction: column;

      .left, .right{
        flex: none;
        width: 100vw;
      }
      
      .left{
        position: static;

        .big-box .box{
          width: 100vw;
        }
      }
    }
  }

  #detail2 .recommend
  .recommendBox{
    gap: 0 5%;
    .box{
      width: 33.33%;
      margin-bottom: 50px;
    }
  }

  #detail2 .section1 .right .text .hashBox .hashTag{
    margin-right: 10px;
    margin-bottom: 5px;
  }
}