#detail{
  width: 100vw;
  height: auto;

  .section1{
    width: 100%;
    height: auto;
    display: flex;

    .img{
      width: 100%;
      height: 100%;
    }
  
    .left{
      flex: 1;
      height: 100vh;
      position: sticky; top: 0;
      overflow: hidden;
  
      .big-box{
        display: flex;
        position: absolute; left: -0vw; top: 0;
        transition: all ease 1s;
        .box{
          flex: none;
          width: 50vw;
          height: 100vh;

          .img{
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
      }
  
      .small-box{
        width: 10%;
        position: absolute; left: 3%; top: 50%;
        transform: translateY(-50%);


  
        .nav{
          margin-bottom: 5px;
          cursor: pointer;

          .img{
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
      }
    }
  
  
    .right{
      background-color: #fff;
      flex: 1;
      width: 50%;
      height: auto;
      padding-top: 150px;
  
      .text{
        padding: 0 10%;
  
        
        .new{
          width: fit-content;
          padding: 5px 10px;
          font-size: 1.3rem;
          color: #f5e2e3;
          background-color: #333;
          border-radius: 3px;
          margin-bottom: 20px;
          font-weight: bold;
        }
  
        .title{
          font-size: 3rem;
          font-weight: bold;
          margin-bottom: 10px;
        }
  
        .description{
          font-size: 1.6rem; 
          margin-bottom: 30px;
        }
  
        .price{
          font-size: 4rem;
          font-weight: bold;
          margin-bottom: 40px;
        }
  
        .hashBox{
          margin-bottom: 50px;
          .hashTag{
            display: inline-block;
            font-size: 1.5rem;
            border: 1px solid #ccc;  
            border-radius: 20px;
            color: #181d90;
            padding: 7px 14px;
            margin-right: 20px;
          }
        }
  
        .buyInfor{
          font-size: 1.5rem;
          text-align: center;
          padding: 15px 10px;
          background-color: #fafafa;
          margin-bottom: 20px;
        }
  
        .buy{
          text-align: center;
          font-size: 2rem;
          padding: 15px 0;
          color: #f5e2e3;
          background-color: #333;
          border-radius: 10px;
          font-weight: bold;
        }
      }
  
      .rightImage{
        position: relative;
        height: auto;
        .tab{
          position: sticky; top: 0;
          padding-top: 120px;
          padding-bottom: 50px;
          height: 50px;
          background-color: #fff;
          margin-bottom: 15px;
          box-shadow: 0 10px 10px -10px #000;
  
  
          .tabMenu{
            display: inline-block;
            width: 33.33%;
            text-align: center;
            background-color: #fff;
            font-size: 1.5rem;
            cursor: pointer;
          }
        }

        .imgBox{
          height: auto;
        }
      }

      .pr{
        padding:50px 10%;
        height: auto;
        background-color: #fff;
        
        .title{
          font-size: 1.5rem;
          font-weight: bold;
          margin-bottom: 40px;
        }

        .tr{
          margin-bottom: 20px;
          display: flex;
        }

        .name{
          flex: none;
          width: 100px;
          font-size: 1.5rem;
          font-weight: bold;
          margin-right: 50px;
        }

        .des{
          font-size: 1.2rem;
        }
      }
      
    }
  }
  

  .recommend{
    width: 100%;
    padding: 50px 0 100px 0;

    .h2{
      font-size: 3.5rem;
      letter-spacing: 5px;
      font-weight: 300;
      text-align: center;
      margin-bottom: 30px;
    }

    .recommendBox{
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      gap: 0 0.5%;

      .box{
        flex: none;
        width: 19%;
        text-align: center;

        .title{
          font-size: 1.8rem;
          margin: 15px 0;
        }

        .price{
          font-size: 1.3rem;
          font-weight: bold;
        }
      }
    }
  }
}

@media screen and (max-width : 720px) {
  #detail{
    .section1{
      flex-direction: column;

      .left, .right{
        flex: none;
        width: 100vw;
      }
      
      .left{
        position: static;

        .big-box .box{
          width: 100vw;
        }
      }
    }
  }

  #detail .recommend
  .recommendBox{
    gap: 0 5%;
    .box{
      width: 33.33%;
      margin-bottom: 50px;
    }
  }

  #detail .section1 .right .text .hashBox .hashTag{
    margin-right: 10px;
    margin-bottom: 5px;
  }
}