#search{
  padding: 150px 2% 100px;

  .noSearch{
    padding-top: 100px;
    font-size: 2rem;
    font-weight: bold;
    height: 50vh;
  }


  .product{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 50px 1%;

    .box{
      width: calc(98%/3);
      display: flex;
      flex-direction: column;
      border-radius: 7px 7px 0 0;
      
      .imgBox{
        flex: 12;
        overflow: hidden;
        border-radius: 7px;
        .img{
          width: 100%;
          height: 100%;
          border-radius: 7px 7px 0 0 ;
          transition: all ease 0.3s;
          object-fit: cover;
          &:hover{
            transform: scale(1.1);
          }
        }
      }

      .text{
        flex: 5;
        background-color: #fafafa;
        padding: 20px 5% 10px;

        .hashTag{
          display: flex;
          .hash{
            font-size: 1.2rem;
            padding-right: 10px;
            color: #686868;
          }
        }

        .name{
          font-size: 1.8rem;
          font-weight: bold;
          margin-bottom: 50px;
        }

        .price{
          width: 100%;
          border: 1px solid #e4e4e4;
          background-color: #fff;
          padding: 7px 10px;
          font-size: 1.6rem;
          font-weight: bold;
        }
      }
    }
  }
}

@media screen and (max-width:700px) {
  #search .product .box{
    width: calc(98%/2);
  }
}