.makeup{
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: space-between;
  gap: 0 0.2%;
  overflow: hidden;

  .caption{
    width: 100%;
    height: 100%;
    text-align: center;
    color: #fff;
    background: rgba(0,0,0, 0.5);
    opacity: 0;
    -webkit-transition: all ease 0.3s;
    -moz-transition: all ease 0.3s;
    -o-transition: all ease 0.3s;
    transition: all ease 0.3s;
    .text{
      width: 100%;
      position: absolute;
      left: 50%;
      top: 50%;
      -webkit-transform: translate(-50%,-50%);
      -moz-transform: translate(-50%,-50%);
      -o-transform: translate(-50%,-50%);
      transform: translate(-50%,-50%);

      .text1{font-size: 4rem; width: 100%;}
      .text2{font-size: 2.5rem;}

    }

  }
  
  .left, .right{
    width: 50%;
    height: 100%;
    position: relative;
  }

  .left{
    cursor: pointer;
  }

  .left:hover .caption, .rightBox:hover .caption{
    opacity: 1;
  }

  .right{
    right: -0.5%;
    display: flex;
    flex-wrap: wrap;
    gap: 1% 1% ;
    
    .rightBox{
      width: 49%;
      height: 50%;
      position: relative;
      cursor: pointer;
      
      &:first-child{
        display: none;
      }
      
      .caption{
        .text{
          .text1{font-size: 2.3rem;}
          .text2{font-size: 1.8rem;}
        }
      }
    }
  }
}

@media screen and (max-width : 750px) {
  .makeup{
    flex-direction: column;
    align-items: start;
    justify-content: start;
    height: auto;
    gap: 0;

    .left , .right{
      flex: none;
      width: 100vw;
      height: 100vh;
    }

    .left{
      margin-bottom: 5px;
    }

    .right{
      gap: 0;
      right: 0;

      .rightBox{
        width: 50%;
      }
    }
  }
}