#product{
  padding: 150px 2% 100px;

  .category{
    display: flex;
    align-items: center;
    margin-bottom: 50px;
    justify-content: center;

    .title{
      font-size: 2.8rem;
      font-weight: bold;
      padding-right: 20px;
    }

    .list{
      display: flex;
      background-color: #e8e8e8;
      border-radius: 7px;
      padding: 10px 50px;
      justify-content: center;

      .item{
        font-size: 1.6rem;
        padding: 0 20px;
        cursor: pointer;
      }
    }
  }

  .product{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 50px 1%;

    .box{
      width: calc(98%/3);
      display: flex;
      flex-direction: column;
      border-radius: 7px 7px 0 0;
      
      .imgBox{
        flex: 12;
        overflow: hidden;
        border-radius: 7px;
        .img{
          width: 100%;
          height: 100%;
          border-radius: 7px 7px 0 0 ;
          transition: all ease 0.3s;
          object-fit: cover;
          &:hover{
            transform: scale(1.1);
          }
        }
      }

      .text{
        flex: 5;
        background-color: #fafafa;
        padding: 20px 5% 10px;

        .hashTag{
          display: flex;
          .hash{
            font-size: 1.2rem;
            padding-right: 10px;
            color: #686868;
          }
        }

        .name{
          font-size: 1.8rem;
          font-weight: bold;
          margin-bottom: 50px;
        }

        .price{
          width: 100%;
          border: 1px solid #e4e4e4;
          background-color: #fff;
          padding: 7px 10px;
          font-size: 1.6rem;
          font-weight: bold;
        }
      }
    }
  }
}

@media screen and (max-width : 700px) {
  #product .product .box{
    width: calc(98%/2)
  }

  #product .category .list{
    padding: 10px;
  }

}

@media screen and (max-width : 500px) {
  #product .category .title{
    display: none;
  }
  #product .category .list .item{
    padding: 0 10px;
  }

}